.header {
 font-size: 3.8em;
}

.form-style {
  display: block;
  width: 100%;
  background-color: #e9ecef;
  padding: 20px;
}
.result-style {
    margin-top: 40px;
    margin-bottom: 40px;
    display: block;
    width: 100%;
    background-color: #f5f5f5;
    min-height: 500px;
    border-radius: 40px;
    padding: 40px;
}
.result-card-style {
  background-color: #4285f4;
  color: #ffffff;
  padding: 15px;
  border-radius: 25px;
}

.card-horizontal {
  display: flex;
}

.image {
  padding: 0;
  overflow: hidden;
  height: 200px;
  margin-bottom: 5px;
}

.description {
  overflow: hidden;
  position: relative;
  max-width: 100%;
}

.btn-group {
  float: right !important;
}
#save {
  margin-right: 5px;

}

